import React, { Component } from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Home from '../home';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import Looks3 from '@material-ui/icons/Looks3';
import LooksTwo from '@material-ui/icons/LooksTwo';
import LooksOne from '@material-ui/icons/LooksOne';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Portrait from '@material-ui/icons/Portrait';
import Edit from '@material-ui/icons/Edit';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import { Link } from 'react-router-dom';


const styles = theme => ({
    button: {
        margin: theme.spacing.unit * 2
    },
    input: {
        display: 'none',
    },
    paper: {
        padding: theme.spacing.unit * 2,
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    alignLeft:{
        marginLeft: '10px',
        textAlign:'left'
    },
    root: {
        width: '100%',
        maxWidth: 360,
        backgroundColor: theme.palette.background.paper,
      },
      nested: {
        paddingLeft: theme.spacing.unit * 4,
      },
})

class ViewPersonal extends Component {
    state = {
        nameList:true,
        _firstName:"",
        _lastName:"",
        _nameID:3
    }

    handleExpand(e){
        let nameListValue = this.state.nameList;
        this.setState({
            nameList: !nameListValue
        })
    }

    componentDidMount() {
      let db = indexedDB.open("MyData");
      db.onsuccess = (event) => {
          let tx = event.target.result.transaction(['Names'], "readonly");
          let store = tx.objectStore('Names');
          let selectedItem = store.get(this.state._nameID);
          selectedItem.onsuccess = (event) => {
              this.setState({
                _firstName : event.target.result.firstName,
                _lastName : event.target.result.lastName
              })
          }
      }
  }
    render() {

        return (
            <div >
                <Home />
     <List
      component="nav"
      subheader={<ListSubheader component="div">Personal Information</ListSubheader>}
      className={this.props.classes.root}
    >
      <ListItem button onClick={this.handleExpand.bind(this)}>
        <ListItemIcon>
          <Looks3 />
        </ListItemIcon>
        <ListItemText primary="Complete Name" />
        {this.state.listItem ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={this.state.nameList} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button className={this.props.classes.nested}>
            <ListItemIcon>
              <Portrait />
            </ListItemIcon>
            <ListItemText primary={this.state._firstName + " " + this.state._lastName} />
            <ListItemSecondaryAction>
                        <IconButton edge="end" component={Link} to={'/editpersonal/'+ this.state._nameID + '/1'} aria-label="Edit">
                      <Edit />
                    </IconButton>
                        </ListItemSecondaryAction>
          </ListItem>
      <ListItem button>
        <ListItemIcon>
          <LooksTwo />
        </ListItemIcon>
        <ListItemText primary="Birth Date" />
      </ListItem>
        </List>
      </Collapse>
    </List>
       

            </div>

        )
    }
}

ViewPersonal.props = {
    classes: PropTypes.object.isRequired
}

export default withStyles(styles)(ViewPersonal);