import React, { Component } from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Home from '../home';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Button from '@material-ui/core/Button';
import {Link} from 'react-router-dom';
import Edit from '@material-ui/icons/Edit';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import ListSubheader from '@material-ui/core/ListSubheader';

const styles = theme => ({
    button: {
        margin: theme.spacing.unit * 2,
    },
    input: {
        display: 'none',
    },
    paper: {
        padding: theme.spacing.unit * 3,
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    alignLeft: {
        marginLeft: "10px",
        marginBottom: "10px"
    }
})

class Bank extends Component {
    state = {
        banksDB: [],
        menu:false,
        _bankName: "",
        _bankID:0
    }

    handleConfirm(bankID,bankName, e){
        let menuValue = this.state.menu;
        this.setState({
            menu: !menuValue,
            _bankID:bankID,
            _bankName:bankName
        })
    }

    handleDelete(e){
        let menuValue = this.state.menu;
        this.setState({
            menu: !menuValue
        })
    }
    
    delete(e) {
        let db = indexedDB.open("MyData");
        db.onsuccess = (event) => {
            let menuValue = this.state.menu;
            let bankID = this.state._bankID;
            let tx = event.target.result.transaction(['banks'], "readwrite");
            let store = tx.objectStore('banks');
            let selectedItem = store.delete(Number(bankID));
            selectedItem.onsuccess = (event) => {
                let selectadd = store.getAll();
                selectadd.onsuccess = (event) => {
                    this.setState({
                        banksDB: event.target.result,
                        menu : !menuValue
                    })
                }
            }
        }
    }
    componentDidMount() {
        let db = indexedDB.open("MyData");
        db.onsuccess = (event) => {
            let tx = event.target.result.transaction(['banks'], "readonly");
            let store = tx.objectStore('banks');
            let selectadd = store.getAll();
            selectadd.onsuccess = (event) => {
                this.setState({
                    banksDB: event.target.result
                })
            }
        }
    }

    render() {

        return (
            <div >
                <Home />
                <div className={this.props.classes.alignLeft} color="inherit">
                  <List dense 
      subheader={<ListSubheader component="div">Registered Banks</ListSubheader>}
      className={this.props.classes.root}>      
                  {this.state.banksDB.length === 0 ?
                  <ListItem>
                  <ListItemText primary="No items" />
                </ListItem>
                  :
                  this.state.banksDB.map(item => (
                        <ListItem key={item.bankId} button>       
                        <Link to={'/editbank/' + item.bankId}>                     
                            <ListItemIcon>
                            <Edit />
                            </ListItemIcon>
                         </Link>
                         <ListItemText
                    primary= {item.name}/>
                        <ListItemSecondaryAction>
                        <IconButton onClick={this.handleConfirm.bind(this,item.bankId, item.name)} edge="end" aria-label="Delete">
                      <DeleteIcon />
                    </IconButton>
                        </ListItemSecondaryAction>
                       
                        </ListItem>
                    ))
                    
                    }
                    </List>
                    <Dialog
        open={this.state.menu}
        onClose={this.handleDelete.bind(this)}  
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Delete Confirmation"}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Are you sure to delete {this.state._bankName} ?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleDelete.bind(this)}  color="primary">
            Disagree
          </Button>
          <Button onClick={this.delete.bind(this)}  color="primary" autoFocus>
            Agree
          </Button>
        </DialogActions>
      </Dialog>  

      <Button variant="outlined" color="primary" component={Link} to="/addbank" className={this.props.classes.button}>
                    Add bank
                </Button>
                </div>
            
       
            </div>

        )
    }
}

Bank.props = {
    classes: PropTypes.object.isRequired
}

export default withStyles(styles)(Bank);