import React, { Component } from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Home from '../home';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

const styles = theme => ({
    button: {
        margin: theme.spacing.unit * 2
    },
    input: {
        display: 'none',
    },
    paper: {
        padding: theme.spacing.unit * 2,
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    alignLeft:{
        marginLeft: '10px',
        textAlign:'left'
    },
    root: {
        width: '100%',
        maxWidth: 360,
        backgroundColor: theme.palette.background.paper,
      },
      nested: {
        paddingLeft: theme.spacing.unit * 4,
      },
})

class EditPersonal extends Component {
    state = {
        _firstName:"",
        _lastName:"",
        _nameID:0
    }
    handleChange(property, e){
        this.setState({
            [property]: e.target.value
        })
    }

    save() {
        var id = Number(this.props.match.params.id);
        var type = Number(this.props.match.params.type);
        let db = indexedDB.open("MyData");
        db.onsuccess = (event) => {
            if(type === 1){
                let tx = event.target.result.transaction(['Names'], "readwrite");
                let store = tx.objectStore('Names');
    
                store.put({
                    nameID : id,
                    firstName: this.state._firstName,
                    lastName: this.state._lastName
                });
    
                tx.oncomplete = (event) => {
                    this.props.history.push("/personal");
                    //e.preventDefault(); // iwas reload
                }
    
                tx.onerror = (event) => {
    
                }
            }
        }
    }
    componentDidMount() {
        var id = Number(this.props.match.params.id);
        var type = Number(this.props.match.params.type);

        let db = indexedDB.open("MyData");
        db.onsuccess = (event) => {
            let tx = event.target.result.transaction(['Names'], "readonly");
            let store = tx.objectStore('Names');
            let selectedItem = store.get(id);
            selectedItem.onsuccess = (event) => {
                this.setState({
                    _firstName : event.target.result.firstName,
                    _lastName : event.target.result.lastName
                })
            }
        }
    }
    render() {

        return (
            <div >
                <Home />
     <List
      component="nav"
      subheader={<ListSubheader component="div">Edit Information</ListSubheader>}
      className={this.props.classes.root}
    >
      <ListItem>
        <ListItemText primary="Complete Name" />
      </ListItem>
      <ListItem>
      <TextField
                    id="standard-name"
                    label="First Name"
                    fullWidth
                    className={this.props.classes.textField}
                    margin="normal"
                    value={this.state._firstName}
                    onChange={this.handleChange.bind(this, "_firstName")}
                />
      </ListItem>
      
      <ListItem>
      <TextField
                    id="standard-name"
                    label="Last Name"
                    fullWidth
                    className={this.props.classes.textField}
                    margin="normal"
                    value={this.state._lastName}
                    onChange={this.handleChange.bind(this, "_lastName")}
                />
      </ListItem>
    </List>
       

      <Button variant="outlined" color="primary" onClick={this.save.bind(this)}  className={this.props.classes.button}>
                    Save
                </Button>

            </div>

        )
    }
}

EditPersonal.props = {
    classes: PropTypes.object.isRequired
}

export default withStyles(styles)(EditPersonal);